import { isMobile } from 'react-device-detect';
import { createRoot } from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';

import { apiCreator, PLATFORM_TYPE, setupMswWorker } from '@shopby/shared';

import '@shopby/shared/styles/common';
import '@shopby/shared/styles/component';
import '@shopby/shared/styles/aurora';

import './assets/style.css';

import App from './App.jsx';
import './i18n';

const URL_MAP = {
  API: {
    alpha: 'https://alpha-shop-api.e-ncp.com',
    real: 'https://shop-api.e-ncp.com',
  },
  STORAGE: {
    alpha: 'https://alpha-storage.e-ncp.com',
    real: 'https://storage.e-ncp.com',
  },
};

const initializeApi = async () => {
  const result = await fetch('/environment.json');
  const { clientId, profile } = await result.json();

  apiCreator({
    baseURL: URL_MAP.API[profile],
    storageURL: URL_MAP.STORAGE[profile],
    headerOption: {
      clientId,
      platform: isMobile ? PLATFORM_TYPE.MOBILE_WEB : PLATFORM_TYPE.PC,
    },
  });

  return { clientId, profile };
};

const renderApp = async () => {
  const usesMock = false; // Whether or not to use msw worker/

  const { clientId, profile } = await initializeApi();

  if (process.env.NODE_ENV === 'development' && usesMock) {
    const worker = setupMswWorker();
    worker.start();
  }

  const app = document.getElementById('app');
  const root = createRoot(app);

  root.render(
    <BrowserRouter>
      <App clientId={clientId} profile={profile} />
    </BrowserRouter>
  );
};

renderApp();
